.title {
  margin: 0;

  &--h1 {
    font-weight: 500;
    font-size: 80px;
    line-height: 80px;

    @include vp-1023 {
      font-size: 60px;
      line-height: 60px;
    }

    @include vp-767 {
      font-size: 45px;
      line-height: 50px;
    }
  }

  &--h3 {
    font-size: 24px;
    line-height: 30px;
    font-weight: 400;

    @include vp-1023 {
      font-size: 22px;
      line-height: 28px;
    }

    @include vp-767 {
      font-size: 16px;
      line-height: 20px;
    }
  }
}
