html.has-scroll-smooth {
  overflow: hidden;
}
html.has-scroll-dragging {
  user-select: none;
}

.has-scroll-smooth body {
  overflow: hidden;
}

.has-scroll-smooth [data-scroll-container] {
  min-height: 100vh;
}
[data-scroll-direction=horizontal] [data-scroll-container] {
  height: 100vh;
  display: inline-block;
  white-space: nowrap;
}

[data-scroll-direction=horizontal] [data-scroll-section] {
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  height: 100%;
}

.c-scrollbar {
  position: absolute;
  right: 0;
  top: 0;
  width: 11px;
  height: 100%;
  transform-origin: center right;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0;
}
.c-scrollbar:hover {
  transform: scaleX(1.45);
}
.c-scrollbar:hover, .has-scroll-scrolling .c-scrollbar, .has-scroll-dragging .c-scrollbar {
  opacity: 1;
}
[data-scroll-direction=horizontal] .c-scrollbar {
  width: 100%;
  height: 10px;
  top: auto;
  bottom: 0;
  transform: scaleY(1);
}
[data-scroll-direction=horizontal] .c-scrollbar:hover {
  transform: scaleY(1.3);
}

.c-scrollbar_thumb {
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  opacity: 0.5;
  width: 7px;
  border-radius: 10px;
  margin: 2px;
  cursor: grab;
}
.has-scroll-dragging .c-scrollbar_thumb {
  cursor: grabbing;
}
[data-scroll-direction=horizontal] .c-scrollbar_thumb {
  right: auto;
  bottom: 0;
}

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/inter-medium.woff2") format("woff2"), url("../fonts/inter-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/inter-regular.woff2") format("woff2"), url("../fonts/inter-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Inter", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 60px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 48px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 36px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 18px;
  }
}

.loader {
  position: fixed;
  z-index: 1000;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #303030;
}
.loader.is-hidden {
  display: none;
}
.loader__box {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.loader__box-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 8px;
}
.loader__box-item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid #303030;
  background-color: #303030;
}
.loader__box-item:before, .loader__box-item:after {
  position: absolute;
  width: 40px;
  height: 40px;
  content: "";
  border-radius: 50%;
}
.loader__box-item:before {
  animation: scale-1 2500ms linear infinite;
  opacity: 0;
  background-color: #ffffff;
}
.loader__box-item:after {
  animation: scale-2 2500ms linear infinite;
  background-color: #303030;
}
.loader__box-item:nth-child(2):before, .loader__box-item:nth-child(2):after {
  animation-delay: 300ms;
}
.loader__box-item:nth-child(3):before, .loader__box-item:nth-child(3):after {
  animation-delay: 600ms;
}
.loader__box-item:nth-child(4):before, .loader__box-item:nth-child(4):after {
  animation-delay: 900ms;
}
.loader__box-item:nth-child(5):before, .loader__box-item:nth-child(5):after {
  animation-delay: 1200ms;
}
.loader__box-item:nth-child(6):before, .loader__box-item:nth-child(6):after {
  animation-delay: 1500ms;
}
.loader__box-item:nth-child(7):before, .loader__box-item:nth-child(7):after {
  animation-delay: 1800ms;
}
.loader__box-item:nth-child(8):before, .loader__box-item:nth-child(8):after {
  animation-delay: 2100ms;
}
.loader__box-item:nth-child(9):before, .loader__box-item:nth-child(9):after {
  animation-delay: 2400ms;
}
@keyframes scale-1 {
  0% {
    z-index: 2;
    transform: scale(0);
    opacity: 1;
  }
  50%, 100% {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes scale-2 {
  0%, 50% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

.header {
  position: fixed;
  z-index: 10;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  padding: 15px 0;
  background-color: rgba(255, 255, 255, 0);
}
.header.is-opened .header__logo {
  color: #ffffff;
  transition: color 0.3s ease;
}
@media (hover: hover) {
  .header.is-opened .header__toggle:hover:not(.focus-visible)::before {
    transform: translate3d(0, 5px, 0) rotate(-39deg);
  }
  .header.is-opened .header__toggle:hover:not(.focus-visible)::after {
    transform: translate3d(0, -6px, 0) rotate(39deg);
  }
}
.header.is-opened .header__toggle.focus-visible:focus::before {
  transform: translate3d(0, 5px, 0) rotate(-39deg);
}
.header.is-opened .header__toggle.focus-visible:focus::after {
  transform: translate3d(0, -6px, 0) rotate(39deg);
}
.header.is-opened .header__toggle::before, .header.is-opened .header__toggle::after {
  background-color: #ffffff;
  transition: transform 0.3s ease, background-color 0.3s ease;
}
.header.is-opened .header__toggle::before {
  transform: translate3d(0, 5px, 0) rotate(-45deg);
}
.header.is-opened .header__toggle::after {
  transform: translate3d(0, -6px, 0) rotate(45deg);
}
.header__layout {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  width: 181px;
  height: 33px;
  color: #303030;
  transition: color 0.3s ease 0.3s;
}
.header__toggle {
  position: relative;
  width: 36px;
  height: 40px;
  padding: 0;
  cursor: pointer;
  border: 0;
  background: 0 0;
}
@media (hover: hover) {
  .header__toggle:hover:not(.focus-visible)::before {
    transform: translate3d(-4px, 0, 0);
  }
  .header__toggle:hover:not(.focus-visible)::after {
    transform: translate3d(4px, 0, 0);
  }
}
.header__toggle.focus-visible:focus::before {
  transform: translate3d(-4px, 0, 0);
}
.header__toggle.focus-visible:focus::after {
  transform: translate3d(4px, 0, 0);
}
.header__toggle::before, .header__toggle::after {
  position: absolute;
  left: 0;
  width: 36px;
  height: 2px;
  content: "";
  transition: transform 0.3s ease, background-color 0.3s ease 0.3s;
  background-color: #303030;
}
.header__toggle::before {
  top: 13px;
}
.header__toggle::after {
  bottom: 13px;
}

.main-nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100vh;
  padding-top: 70px;
  transition: clip-path cubic-bezier(0.55, 0, 0.44, 0.99) 0.7s;
  pointer-events: none;
  background-color: #303030;
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
}
.main-nav.is-opened {
  pointer-events: all;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 110%);
}
.main-nav.is-opened .main-nav__item:nth-child(1) .main-nav__link {
  transition-delay: 0.33s;
}
.main-nav.is-opened .main-nav__item:nth-child(2) .main-nav__link {
  transition-delay: 0.41s;
}
.main-nav.is-opened .main-nav__item:nth-child(3) .main-nav__link {
  transition-delay: 0.49s;
}
.main-nav.is-opened .main-nav__item:nth-child(4) .main-nav__link {
  transition-delay: 0.57s;
}
.main-nav.is-opened .main-nav__item:nth-child(5) .main-nav__link {
  transition-delay: 0.65s;
}
.main-nav.is-opened .main-nav__link {
  transform: translate3d(0, 0, 0);
}
.main-nav__wrapper {
  overflow: auto;
  width: 100%;
  height: 100%;
  padding: 70px 0;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  overflow: hidden;
}
.main-nav__item:not(:last-of-type) {
  margin-bottom: 35px;
}
.main-nav__link {
  font-size: 50px;
  line-height: 60px;
  display: block;
  transition: transform 0.3s ease;
  transform: translate3d(0, 110%, 0);
  color: #ffffff;
}
@media (max-width: 767px) {
  .main-nav__link {
    font-size: 32px;
    line-height: 40px;
  }
}
.main-nav__link.is-active {
  opacity: 0.6;
}
.main-nav__toggle {
  display: none;
}

.footer {
  padding: 40px 0;
  background-color: #2c39f2;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
