.intro[data-animate=intro].is-shown .card {
  transform: scale(1);
  opacity: 1;
}
@media (hover: hover) {
  .intro[data-animate=intro].is-shown .card:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.intro[data-animate=intro].is-shown .card.focus-visible:focus {
  opacity: 0.7;
}
.intro[data-animate=intro].is-shown .intro__heading .word::after {
  transform: translate3d(0, 0, 0);
}
.intro__sticky {
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  max-height: 100vh;
  max-height: calc(100 * var(--vh, 1vh));
  perspective: 1000px;
}
.intro__heading {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding-top: 40px;
}
@media (max-width: 767px) {
  .intro__heading {
    flex-direction: column;
    align-items: flex-start;
  }
}
.intro__heading .word {
  position: relative;
  overflow: hidden;
  color: rgba(255, 255, 255, 0);
}
.intro__heading .word:after {
  position: absolute;
  top: -4px;
  left: 0;
  content: attr(data-word);
  transition: transform 0.4s ease;
  transition-delay: calc(0.08s * var(--line-index));
  transform: translate3d(0, 100%, 0);
  color: #303030;
}
.intro__title {
  color: #303030;
}
.intro__text {
  font-size: 18px;
  line-height: 24px;
  margin: 0;
  text-align: right;
  color: #303030;
}
@media (max-width: 767px) {
  .intro__text {
    margin-top: 20px;
    text-align: left;
  }
}
.intro__cards {
  width: 67%;
  margin: 80px 0 0 auto;
  padding: 0;
}
@media (max-width: 1279px) {
  .intro__cards {
    width: 100%;
  }
}
.intro__cards.content-grid-layout {
  column-gap: 40px;
  row-gap: 25px;
}
@media (max-width: 1023px) {
  .intro__cards.content-grid-layout {
    column-gap: 30px;
    row-gap: 20px;
  }
}
@media (max-width: 767px) {
  .intro__cards.content-grid-layout {
    grid-template-columns: repeat(2, 1fr);
  }
}
.intro__bg {
  position: absolute;
  z-index: -1;
  overflow: hidden;
  width: 100%;
  height: 100%;
  pointer-events: none;
  mix-blend-mode: color-dodge;
}
.intro__bg video {
  position: absolute;
  top: -2%;
  left: -2%;
  width: 104%;
  max-width: 104%;
  height: 104%;
  max-height: 104%;
  opacity: 0.4;
  object-fit: cover;
  filter: blur(8px) contrast(1) sepia(1) grayscale(1) brightness(0.7);
}

.section {
  position: relative;
  background-color: #ffffff;
}

.content-block {
  margin: 0 auto;
}
.content-block h1:not(:last-child) {
  margin-bottom: 60px;
}
.content-block h2 {
  font-size: 45px;
  font-weight: 400;
  line-height: 54px;
  margin: 0;
}
.content-block h2:not(:last-child) {
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .content-block h2 {
    font-size: 34px;
    line-height: 42px;
  }
}
.content-block p {
  font-size: 18px;
  font-weight: 400;
  line-height: 26px;
  margin: 0;
}
.content-block p:not(:last-child) {
  margin-bottom: 15px;
}
@media (max-width: 767px) {
  .content-block p {
    font-size: 16px;
    line-height: 22px;
  }
}
.content-block strong {
  font-weight: 700;
}

.card {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  min-height: 115px;
  padding: 20px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.6);
}
@media (max-width: 1023px) {
  .card {
    padding: 15px;
    min-height: 110px;
  }
}
@media (max-width: 767px) {
  .card {
    min-height: unset;
  }
}
.card--gainsboro {
  background-color: #e3e3e3;
}
.card--centered {
  align-items: center;
  justify-content: center;
}
.card[data-animation-preset=fade] {
  opacity: 0;
}
[data-animation-preset=fadeIn] .card[data-animation-preset=item] {
  transform: translate3d(0, 10%, 0);
  opacity: 0;
}
[data-animation-preset=fadeScale] .card[data-animation-preset=item] {
  transform: scale(0);
  opacity: 0;
}
.card__text {
  text-align: right;
  color: #303030;
}

a.card {
  cursor: pointer;
}
@media (hover: hover) {
  a.card:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
a.card.focus-visible:focus {
  opacity: 0.7;
}

.map {
  width: 100%;
  height: 100%;
}

.map-balloon {
  max-width: 400px;
  width: max-content;
  display: flex;
  box-shadow: 8px 8px 13px 1px rgba(34, 60, 80, 0.2);
  z-index: 3;
  transition: opacity 0.6s ease, visibility 0.6s ease, transform 0.6s ease;
  min-height: 100px;
  position: relative;
  overflow: hidden;
  border-radius: 20px;
  transform: translateX(-50%);
}
.map-balloon__photo {
  position: relative;
  width: 100px;
  max-width: none;
  min-height: 100px;
  flex-shrink: 0;
  overflow: hidden;
}
.map-balloon__photo img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.map-balloon__content {
  position: relative;
  background-color: #ffffff;
  padding: 10px 26px 10px 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.map-balloon__text {
  color: #aaa5a0;
  font-size: 10px;
  line-height: 14px;
  margin: 0 0 10px 0;
}
.map-balloon__name {
  font-weight: 600;
  margin: 0 0 5px 0;
  font-size: 14px;
  line-height: 18px;
}
.map-balloon__address {
  font-size: 10px;
  line-height: 14px;
  margin: 0;
}
.map-balloon__close-button {
  background-color: rgba(255, 255, 255, 0);
  padding: 0;
  position: absolute;
  right: 10px;
  top: 10px;
  width: 12px;
  height: 12px;
  border: none;
  cursor: pointer;
}
.map-balloon__close-button svg {
  width: 100%;
  height: 100%;
  color: #000000;
}

.map-message {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.3s ease;
  pointer-events: none;
  opacity: 0;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.3);
}
.map-message.is-shown {
  opacity: 1;
  pointer-events: all;
  z-index: 1;
}
.map-message p {
  pointer-events: none;
  color: #ffffff;
}

.main-page__section {
  padding-top: 90px;
}
@media (max-width: 767px) {
  .main-page__section {
    padding-top: 45px;
  }
}
.main-page__section--extra-padding {
  padding-bottom: 90px;
}
.main-page__scroll-slider {
  margin-top: 200px;
  padding-top: 0;
}
.main-page__content-block {
  max-width: 70%;
}
@media (max-width: 767px) {
  .main-page__content-block {
    max-width: 100%;
  }
}

.scroll-slider {
  position: relative;
}
.scroll-slider__sticky {
  position: sticky;
  top: 0;
  right: 0;
  left: 0;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  max-height: 100vh;
  max-height: calc(100 * var(--vh, 1vh));
}
.scroll-slider__slide {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  max-height: 100vh;
  max-height: calc(100 * var(--vh, 1vh));
  transition: 0.3s ease;
  transform: translate3d(0, 0, 0);
  pointer-events: none;
  opacity: 0;
}
.scroll-slider__slide.is-active {
  opacity: 1;
}
.scroll-slider__slide-content {
  position: relative;
  max-width: 70%;
  margin: 0 auto;
  transform: translate3d(0, 0, 0);
  text-align: center;
  z-index: 1;
}
.scroll-slider__slide-content::before {
  position: absolute;
  z-index: -1;
  top: -50%;
  right: -10%;
  bottom: -30%;
  left: -10%;
  width: 120%;
  height: 200%;
  content: "";
  opacity: 0.6;
  border-radius: 50%;
  background-color: #ffffff;
  filter: blur(70px);
}
.scroll-slider__slide-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: repeat;
}

.map-page {
  padding-top: 120px;
  padding-bottom: 30px;
}
.map-page__map-wrapper {
  position: relative;
  display: flex;
  overflow: hidden;
  width: 100%;
  height: 700px;
  margin: 100px 0 0;
  border-radius: 20px;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.4);
}
.map-page .content-block strong {
  font-size: 24px;
  font-weight: 700;
  line-height: 27.6px;
  display: block;
  margin-bottom: 20px;
}
.map-page .content-block p:not(:last-child) {
  margin-bottom: 16px;
}
.map-page__filters-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -30px;
}
.map-page__filters-item {
  display: block;
  margin-top: 30px;
  margin-left: 30px;
}
.map-page__filters-item:first-child {
  transform: scale(1.1);
  margin-left: 35px;
  margin-right: 10px;
}
.map-page__filters-item input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.map-page__filters-item input:checked {
  pointer-events: none;
}
.map-page__filters-item input:checked + label {
  background-color: #6bcaff;
  pointer-events: none;
}
@media (hover: hover) {
  .map-page__filters-item input:hover:not(.focus-visible) + label {
    opacity: 0.65;
  }
  .map-page__filters-item input:hover:not(.focus-visible) + label img {
    transform: scale(1.1);
  }
}
.map-page__filters-item input.focus-visible:focus + label {
  opacity: 0.65;
}
.map-page__filters-item input.focus-visible:focus + label img {
  transform: scale(1.1);
}
.map-page__filters-item label {
  font-size: 18px;
  line-height: 21px;
  display: flex;
  align-items: center;
  padding: 10px 20px 10px 5px;
  cursor: pointer;
  user-select: none;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  border-radius: 15px;
  background-color: rgba(107, 202, 255, 0.5);
}
@media (hover: hover) {
  .map-page__filters-item label:hover:not(.focus-visible) {
    opacity: 0.65;
  }
  .map-page__filters-item label:hover:not(.focus-visible) img {
    transform: scale(1.1);
  }
}
.map-page__filters-item label.focus-visible:focus {
  opacity: 0.65;
}
.map-page__filters-item label.focus-visible:focus img {
  transform: scale(1.1);
}
.map-page__filters-item img {
  transition: transform 0.3s ease;
}

